@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  /* background-color: #179bc4; */
  background-color: #4158D0;
  background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
  font-family: 'Open Sans', sans-serif;


}


.app {
    position: absolute;
    top: 10%;
    left: 50%;
    -webkit-transform: translate(-50%, -10%);
            transform: translate(-50%, -10%);
  }
  
  .app .mainHeading h1 {
    text-align: center;
  }
  
  .app h1,
  h2,p {
    color: white;
    text-align: center;
  }
  
  .app .input {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .app .input {
    margin-top: 0.8em;
    height: 2em;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 6px;
    padding: 5px;
  }
  
  .app .input input {
    width: 100%;
    height: 1.6em;
    border-color: transparent;
    outline-color: transparent;
  }
  
  .app i {
    cursor: pointer;
    font-size: larger;
    font-weight: 900;
    color: grey;
    margin-right: 5px;
  }
  .app .todos {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 1rem;
  }
  .app .todos .todo {
    margin-top: 0.8em;
    width: 20em;
    height: 2em;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 6px;
    padding: 5px;
  }
  .app .todos .todo .left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  
  .app .todos .todo .left p {
    margin-left: 3px;
    font-size: medium;
    font-weight: 700;
    font-style: normal;
    color: grey;
  }
  
  .task-display{
    display: flex;
    justify-items: center;
    align-items: center;
    margin-top: 5rem;
  }
  
  .progress{
    margin-right: 20px;
    padding-right: 20px;
    justify-content: space-between;
    border-right: 1px solid #fff;
  }
  
  .task{
    padding-top: 20px;
    color: #fff;
    text-align: center;
  }
  
  .app .todos .todo .left .strike {
      text-decoration: line-through;
      -webkit-text-decoration-color: #020631;
              text-decoration-color: #020631;
  }
